var mobe = mobe || {};

mobe = new function() {

    this.checkedForm = false;

    this.db = document.body;

    this.checkPolicy = () => document.getElementById('haveRead').checked;

    this.setFocusComp = function() {

        const elem = document.getElementById("contForm");
        let inputo = elem.querySelectorAll('.block-text input');
        inputo.forEach(function(item, i, arr){
            item.addEventListener("focus", function() {
                this.parentNode.classList.add('focused');
            }, true);
            item.addEventListener("blur", function() {
                this.parentNode.classList.remove('focused');
            }, true);
        });
    };

    this.lostFocusComp = function() {
        var self = this;

        let blockText = document.querySelectorAll('.block-text input');
        for (let i = 0; i < blockText.length; i++) {
            let type = blockText[i].getAttribute('type');

            blockText[i].addEventListener("input", function(ev) {
                self.check(type, blockText[i]);
            });
        }
    };

    this.check = function(type, item) {
        self = this;

        switch (type) {
            case 'text':
                if (!item.validity.valid) {
                    item.parentNode.classList.remove('valid');
                    item.parentNode.classList.add('novalid');
                    self.checkedForm = false;
                } else {
                    item.parentNode.classList.remove('novalid');
                    item.parentNode.classList.add('valid');
                    self.checkedForm = true;
                }
                break;
            case 'email':
                if (item.validity.typeMismatch) {
                    item.parentNode.classList.remove('valid');
                    item.parentNode.classList.add('novalid');
                    self.checkedForm = false;
                } else {
                    item.parentNode.classList.remove('novalid');
                    item.parentNode.classList.add('valid');
                    self.checkedForm = true;
                }
                break;
            case 'phone':
                if (item.validity.patternMismatch) {
                    item.parentNode.classList.remove('valid');
                    item.parentNode.classList.add('novalid');
                    self.checkedForm = false;
                } else {
                    item.parentNode.classList.remove('novalid');
                    item.parentNode.classList.add('valid');
                    self.checkedForm = true;
                }
                break;
            default:
                break;
        };
    };

    this.checkFormWeiter = function() {
        var self = this;
        let regWeiter = document.getElementById('contSend'),
        showMessage = document.querySelectorAll('.show-warn');
        regWeiter.addEventListener('click', function() {
            if(self.checkedForm && self.checkPolicy()) {
                self.sendMess();
                showMessage[0].textContent = "";
                self.successForm();
            } else {
                showMessage[0].textContent = "Alle Felder sind erforderlich, bitte überprüfe dein Eingaben.";
            }
        });
    };

    this.sendMess = function() {
        console.log('get here message for Contact page');
    }

    this.successForm = function() {
        const foote = document.getElementById("pageFooter");
        const scBlanket = `
            <div id="sucsBlanket" class="sucs-blanket open abbrech">
                <div class="popup">
                    <span class="modal-close abbrech">&#43;</span>
                    <h5 class="bl-title">Contact us</h5>
                    <p class="sucs-icon"><span class="check-ico"></span></p>
                    <p class="sucs-mess">Your message has been successfully send! Our managers will answer you during the working day.</p>
                    <p class="sucs-btn"><a href="/contact-page.html" id="newReg" class="btn btn-yellow">Continue</a></p>
                </div>
            </div>
            `;
        foote.insertAdjacentHTML('afterend', scBlanket);
    }

    this.init = function() {
        let self = this;
        self.db.addEventListener('click', function(ev){
            if(document.querySelector(".abbrech") && ev.target.classList.contains('abbrech')){
                self.closeBlanket();
            }
        });
    };

    this.closeBlanket = function() {

        const sucsBl = document.getElementById("sucsBlanket");
        sucsBl.animate([
            {opacity: 1},
            {opacity: 0}
        ], {
            duration: 450,
            fill: 'forwards'
        });

        setTimeout(function() {
            sucsBl.remove();
        }, 450);
    };
};

document.addEventListener('DOMContentLoaded', function() {
    mobe.setFocusComp();
    mobe.lostFocusComp();
    mobe.checkFormWeiter();
    mobe.init();
}, false);
